import React from 'react';

const Card = ({name, email, id}) => {

    return (
        <div className="tc grow bg-dark-blue br3 pa3 ma2 dib bw2 shadow-5">
            <img src={`https://robohash.org/${id}`} alt="placeholder" />
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
            </div>
        </div>
    );
}

export default Card;